.FilmDetail {
  display: flex;
  min-height: 100vh;  
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
  margin-bottom: 2em;
}

.FilmDetail .rating_18x {
  max-width: 64px;
}

.FilmDetail .rating_18x:hover {
  transform: scale(1.5);
}

.FilmDetail > p {
  text-align: center;
}

.FilmDetail > p > .material-icons {
  display: block;
  font-size: 3em;
  margin-bottom: 0.25em;
}

.FilmDetail.is-hydrated {
  display: block;
}

.FilmDetail .film-backdrop {
  position: relative;  
}

.FilmDetail .film-title {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0;
  color: white;
  font-size: 4em;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  background: linear-gradient(180deg, transparent, rgba(0,0,0,0.5));
  padding: 0.25em 1.5rem;
}

.FilmDetail .film-detail-poster {
  float: right;
  width: 20%;
  margin-left: 2em;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.35);
}

.FilmDetail .film-meta {
  padding: 1em 1.5em;
  line-height: 1.45;
  color: white;
  background-color: lightseagreen;
}

.FilmDetail .film-meta .film-tagline {
  font-style: italic;
}